import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Home from './Components/Home';
import About from './Components/About';
import Contact from './Components/Contact';
import Footer from './Components/Footer'
class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <h2>Welcome to day-12  React Router Tutorial</h2>
          <nav className="navbar  fixed-top  navbar-expand-sm navbar-light bg-light">
            <ul className="navbar-nav mr-4 ">
              <li><Link to={'/'} className="nav-link pr-4"> Home </Link></li>
              <li><Link to={'/contact'} className="nav-link pr-4">Contact</Link></li>
              <li><Link to={'/about'} className="nav-link pr-4">About</Link></li>
            </ul>
          </nav>
          <hr />
         
          <Switch>
            <Route exact path='/' component={Home} />
            <Route path='/contact' component={Contact} />
            <Route path='/about' component={About} />
          </Switch>
        </div>
       <div>
         <Footer/>
       </div>
      </Router>
    );
  }
}

export default App;